import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import {
  InputDate,
  ReanderField,
  ReanderSelect,
} from "../../../components/helpers/field";
import { getImage, getTglSystem } from "../../../components/helpers/function";
import Tabel from "../../../components/helpers/tabel";
import Loading from "react-fullscreen-loading";
import {
  GetDataGroup,
  getDataJenis,
  getMasterToko,
  showModal,
} from "../../../actions/datamaster_action";
import { Gramasi } from "../../../Validasi/normalize";
import { getData, getDataNoTimeOut } from "../../../config/axios";
import { ToastNotification } from "../../../components/helpers/notification";
import { imgfound } from "../../../components/helpers/library";
import ModalGambar from "./ModalGambar";
import ModalGlobal from "./../../../components/helpers/ModalGlobal";

const maptostate = (state) => {
  return {
    initialValues: {
      dari_tanggal: getTglSystem(),
      sampai_tanggal: getTglSystem(),
    },
    getMasterToko: state.datamaster.getMasterToko,
    GetDataGroup: state.datamaster.GetDataGroup,
    getDataJenis: state.datamaster.getDataJenis,
    datakondisi: state.datamaster.getDataKondisiBarang,
    setHancurBarang: state.databarang.setHancurBarang,
  };
};
class HeadTransaksiMember extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      point: "POINT",
      kondisi_barang: "kosong",
      isLoading: false,
      isLoadingBatal: false,
      readOnly: false,
      gambar: imgfound,
      kategori: "SEMUA",
      columns: [
       
        {
          dataField: "tgl_trx",
          text: "Tanggal Trx",
          headerStyle: (colum, colIndex) => {
            return { width: "100px" };
          },
        },
        {
          dataField: "kategori",
          text: "Kategori",
        },
        {
          dataField: "kode_barcode",
          text: "Barcode",
        },
        // {
        //   dataField: "kode_group",
        //   text: "Kategori",
        //   headerStyle: (colum, colIndex) => {
        //     return { width: '100px'};
        //   },
        // },
        {
          dataField: "deskripsi",
          text: "Deskripsi",
        },
        {
          dataField: "berat",
          text: "Berat",
          headerStyle: (colum, colIndex) => {
            return { width: "100px", textAlign: "right" };
          },
          formatter: (cell) => {
            return (
              <div className="text-right">
                {parseFloat(cell || 0).toFixed(3)}
              </div>
            );
          },
        },
        {
          dataField: "jumlah_rp",
          text: "Jumlah Rp",
          headerStyle: (colum, colIndex) => {
            return { width: "100px", textAlign: "right" };
          },
          headerClasses: "text-right",
          formatter: (cell) => {
            return (
              <div className="text-right">
                {parseFloat(cell || 0).toLocaleString("kr-KO")}
              </div>
            );
          },
        },
        {
          dataField: "poin_awal",
          text: "Poin Awal",
          headerStyle: (colum, colIndex) => {
            return { width: "100px", textAlign: "right" };
          },
          formatter: (value, row) => {
            return <div className="text-right"> {value || 0} </div>;
          },
        },
        {
          dataField: "poin",
          text: "Poin",
          headerStyle: (colum, colIndex) => {
            return { width: "100px", textAlign: "right" };
          },
          formatter: (value, row) => {
            return <div className="text-right"> {value || 0} </div>;
          },
        },
        {
          dataField: "poin_akhir",
          text: "Poin Akhir",
          headerStyle: (colum, colIndex) => {
            return { width: "100px", textAlign: "right" };
          },
          formatter: (value, row) => {
            return <div className="text-right"> {value || 0} </div>;
          },
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            return (
              <div className="row text-center">
                <div
                  className={
                    row.kategori === "PENJUALAN" ? "col-12" : "col-12 d-none"
                  }
                >
                  <button
                    type="button"
                    onClick={() => this.getImages(row)}
                    className="btn btn-success"
                  >
                    <i className="fa fa-eye"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
  }
  getImages(row) {
    // let kategori = this.state.kategori
    let type_pencarian = this.state.type_pencarian;
    this.setState({
      isLoading: true,
    });
    if (type_pencarian === "LAMA") {
      getDataNoTimeOut(
        "barang/image/lama?kode_barcode=" + row.kode_barcode.trim()
      )
        .then((res) => {
          this.setState({ gambar: res.data.url, isLoading: false });
          this.props.dispatch(showModal());
        })
        .catch((err) => {
          this.setState({
            isLoading: false,
            gambar : imgfound
          });
          ToastNotification("info", "Gambar Tidak Ada");
        });
    }else{
      getImage('foto_produk/'+row.kode_barcode.trim())
      .then((res) => {
        this.setState({
          isLoading: false,
          gambar : res
        });
        this.props.dispatch(showModal());
      }).catch((err) => {
        this.setState({
          isLoading: false,
          gambar : imgfound
        });
        ToastNotification("info", "Gambar Tidak Ada");
      })
    }
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }
  componentDidMount() {
    this.props.dispatch(GetDataGroup());
    this.props.dispatch(getDataJenis());
    this.props.dispatch(getMasterToko());
    this.props.change("type_pencarian", "BARU");
    setTimeout(() => {
      document.getElementById("kode_member").focus();
    }, 100);
    this.props.change("kategori", "ALL");
    this.props.change("toko", "ALL");
    this.props.change("history", "TIDAK");
    this.props.change("kode_dept", "ALL");
    this.props.change("kode_group", "ALL");
    this.props.change("tipe_barang", "ALL");
  }
  chnageDataGroup(e) {
    this.props.change("kode_dept", "ALL");
    this.setState({ kode_group: e });
  }
  history(e) {
    console.log(e);
    if (e === "YA") {
      this.setState({
        readOnly: true,
      });
    } else {
      this.setState({
        readOnly: false,
      });
    }
  }
  getKodeMember(e) {
    if (e.length < 10) {
      this.props.change("nama_member", "");
      this.props.change("alamat", "");
      this.props.change("no_hp", "");
      this.setState({ point: 0 });
      return false;
    }
    this.props.getKodeMember(e);
    if (this.delayTimer) {
      clearTimeout(this.delayTimer);
    }
    this.delayTimer = setTimeout(() => {
      if (e === undefined) {
        return false;
      }
      this.setState({
        isLoading: true,
      });
      getData("member/get/by-kode-member/" + e)
        .then((res) => {
          // console.log(res.data)
          this.setState({
            isLoading: false,
          });
          res.data.forEach((item, index) => {
            this.props.change("nama_member", item.nama_customer);
            this.props.change("alamat", item.alamat_customer);
            this.props.change("no_hp", item.no_hp);
            this.setState({
              datatransaksimember: [],
              point: item.point || 0,
              isLoading: false,
            });
          });
          // console.log(res.data);
        })
        .catch((err) => {
          ToastNotification("info", "Data member tidak ada");
          this.setState({
            datatransaksimember: [],
            isLoading: false,
          });
        });
    }, 1500);
  }
  render() {
    let datagroup = [
      {
        value: "ALL",
        name: "SEMUA",
      },
    ];
    this.props.GetDataGroup.forEach((element) => {
      let row = {
        value: element.kode_group,
        name: element.kode_group,
      };
      datagroup.push(row);
    });

    let datatoko = [
      {
        value: "ALL",
        name: "SEMUA",
      },
    ];
    this.props.getMasterToko.forEach((element) => {
      let row = {
        value: element.kode_toko,
        name: element.kode_toko,
      };
      datatoko.push(row);
    });
    let group = [
      {
        value: "ALL",
        name: "SEMUA",
      },
    ];
    this.props.GetDataGroup.forEach((element) => {
      let row = {
        value: element.kode_group,
        name: element.kode_group,
      };
      group.push(row);
    });
    let dept = [
      {
        value: "ALL",
        name: "SEMUA",
      },
    ];
    this.props.getDataJenis
      .filter((cek) => cek.kode_group === this.state.kode_group)
      .forEach((element) => {
        let row = {
          value: element.kode_dept,
          name: element.kode_dept,
        };
        dept.push(row);
      });
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-8">
            <div className="row">
              <div className="col-lg-6">
                <Field
                  onChange={(e) => this.getKodeMember(e.target.value)}
                  id="kode_member"
                  name="kode_member"
                  component={ReanderField}
                  type="text"
                  label="Kode Member"
                  placeholder="Masukan Kode Member"
                />
              </div>
              <div className="col-lg-6">
                <Field
                  id="lihat_transaksi_nama_member"
                  name="nama_member"
                  readOnly={true}
                  component={ReanderField}
                  type="text"
                  label="NAMA CUSTOMER"
                  placeholder="Masukan NAMA CUSTOMER"
                />
              </div>
            </div>
          </div>
          <div className="col-4 text-center">
            <> &nbsp; </>
            <h1
              style={
                this.state.point === "POINT"
                  ? {
                      fontSize: "4.875rem",
                      position: "absolute",
                      left: "90px",
                      top: "100px",
                    }
                  : {
                      fontSize: "4.875rem",
                      position: "absolute",
                      left: "150px",
                      top: "100px",
                    }
              }
            >
              {" "}
              {this.state.point}{" "}
            </h1>
          </div>
          <div className="col-8">
            <div className="row">
              <div className="col-lg-6">
                <Field
                  id="lihat_transaksi_alamat"
                  name="alamat"
                  readOnly={true}
                  component={ReanderField}
                  type="text"
                  label="Alamat"
                  placeholder="Masukan Alamat"
                />
              </div>
              <div className="col-lg-6">
                <Field
                  id="lihat_transaksi_no_hp"
                  name="no_hp"
                  readOnly={true}
                  component={ReanderField}
                  type="text"
                  label="No Hp"
                  placeholder="Masukan No Hp"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="dari_tanggal"
                  component={InputDate}
                  type="text"
                  selected={this.state.tgl_awal}
                  onChange={(date) => this.setStartDate(date)}
                  label="Dari Tanggal"
                  readOnly={this.state.readOnly}
                  placeholder="Masukan Dari Tanggal"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="sampai_tanggal"
                  component={InputDate}
                  type="text"
                  selected={this.state.tgl_akhir}
                  onChange={(date) => this.setLastDate(date)}
                  label="Sampai Tanggal"
                  readOnly={this.state.readOnly}
                  placeholder="Masukan Sampai Tanggal"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="history"
                  component={ReanderSelect}
                  type="text"
                  onChange={(e) => this.history(e)}
                  options={[
                    {
                      name: "YA",
                      value: "YA",
                    },
                    {
                      name: "TIDAK",
                      value: "TIDAK",
                    },
                  ]}
                  label="History"
                  placeholder="Masukan History"
                />
              </div>
              <div className="col-lg-2">
                <Field
                  name="berat_awal"
                  component={ReanderField}
                  type="text"
                  normalize={Gramasi}
                  label="Berat Awal"
                  placeholder="Masukan Berat Awal"
                />
              </div>
              <div className="col-lg-2">
                <Field
                  name="berat_akhir"
                  component={ReanderField}
                  type="text"
                  normalize={Gramasi}
                  label="Berat Akhir"
                  placeholder="Masukan Berat Akhir"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="kode_group"
                  component={ReanderSelect}
                  onChange={(e) => {
                    this.chnageDataGroup(e);
                  }}
                  options={group}
                  type="text"
                  label="Kode Group"
                  placeholder="Masukan Kode Group"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="kode_dept"
                  component={ReanderSelect}
                  type="text"
                  options={dept}
                  label="Kode Jenis"
                  placeholder="Masukan Kode Jenis"
                />
              </div>
              <div className="col-lg-2">
                <Field
                  name="tipe_barang"
                  component={ReanderSelect}
                  type="text"
                  options={[
                    {
                      value: "ALL",
                      name: "SEMUA",
                    },
                    {
                      value: "SP",
                      name: "SP",
                    },

                    {
                      value: "A",
                      name: "A",
                    },
                  ]}
                  label="Tipe Barang"
                  placeholder="Masukan Tipe Barang"
                />
              </div>
            </div>
          </div>
          <div className="col-4 text-center">
            {/* <Field
              name="point"
              readOnly={true}
              component={ReanderField}
              type="text"
              placeholder="Point Saya"
            /> */}
          </div>
          <div className="col-lg-2">
            <Field
              id="kategori_trx"
              name="kategori"
              component={ReanderSelect}
              onChange={(e) => {
                this.setState({ kategori: e });
              }}
              type="text"
              options={[
                {
                  name: "SEMUA",
                  value: "ALL",
                },
                {
                  name: "PENJUALAN",
                  value: "PENJUALAN",
                },
                {
                  name: "PEMBELIAN UMUM",
                  value: "PEMBELIAN UMUM",
                },
                {
                  name: "PESANAN",
                  value: "PESANAN",
                },
                {
                  name: "PESANAN SERAH",
                  value: "PESANAN SERAH",
                },
                {
                  name: "PESANAN TAMBAH",
                  value: "PESANAN TAMBAH",
                },
                {
                  name: "PINJAMAN",
                  value: "PINJAMAN",
                },
                {
                  name: "PINJAMAN LUNAS",
                  value: "PINJAMAN LUNAS",
                },
                {
                  name: "SERVICE",
                  value: "SERVICE",
                },
                {
                  name: "SERVICE AMBIL",
                  value: "SERVICE AMBIL",
                },
                {
                  name: "TUKAR POINT",
                  value: "TUKAR POINT",
                },
                {
                  name: "TAMBAH POIN MANUAL",
                  value: "TAMBAH POIN MANUAL",
                },
                {
                  name: "AMBIL POIN MANUAL",
                  value: "AMBIL POIN MANUAL",
                },
              ]}
              label="Kategori"
              placeholder="Masukan Kategori"
            />
          </div>
          <div className="col-lg-2">
            <Field
              name="toko"
              component={ReanderSelect}
              type="text"
              options={datatoko}
              label="Toko"
              placeholder="Masukan Toko"
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="type_pencarian"
              component={ReanderSelect}
              onChange={(e) => {
                this.setState({ type_pencarian: e });
              }}
              type="text"
              options={[
                {
                  name: "LAMA",
                  value: "LAMA",
                },
                {
                  name: "BARU",
                  value: "BARU",
                },
              ]}
              label="Pilih Transaksi"
              placeholder="Pilih Transaksi"
            />
          </div>
          <div className="col-lg-4">
            <div className="row">
              <div className="col-lg-6">
                <label>&nbsp;</label>
                <button
                  type="submit"
                  value="PDF"
                  name="type_btn"
                  className="btn btn-primary btn-block"
                >
                  {this.props.isLoading ? (
                    <>
                      <i className="fas fa-spinner fa-spin"></i> &nbsp; Lihat
                    </>
                  ) : (
                    "Lihat"
                  )}
                </button>
              </div>
              <div className="col-lg-6">
                <label>&nbsp;</label>
                <button
                  type="button"
                  onClick={this.props.batalLihatTransaksi}
                  className="btn btn-warning btn-block"
                >
                  {this.props.isLoadingBatal ? (
                    <>
                      <i className="fas fa-spinner fa-spin"></i> &nbsp; Batal
                    </>
                  ) : (
                    "Batal"
                  )}
                </button>
              </div>
            </div>
          </div>

          <div className="col-12">
            <Tabel
              keyField="kode_barcode"
              exportCsv={true}
              data={this.props.datatransaksimember}
              columns={this.state.columns}
              empty={true}
              textEmpty="Data Transaksi Member Kosong"
            />
          </div>
        </div>
        <ModalGlobal
          size="P"
          title="Data Gambar"
          content={
            <ModalGambar
              isLoading={this.state.isLoading}
              gambar={this.state.gambar}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          }
        />
        {this.props.isLoadingBatal || this.state.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </form>
    );
  }
}

HeadTransaksiMember = reduxForm({
  form: "HeadTransaksiMember",
  enableReinitialize: true,
})(HeadTransaksiMember);
export default connect(maptostate, null)(HeadTransaksiMember);
