import React from "react";
import { Link } from "react-router-dom";
import {
  Panel,
  PanelBody,
  PanelHeader,
} from "../../../components/panel/panel.jsx";
import HeadTransaksiMember from "./HeadTransaksiMember.jsx";
import {
  formatDate,
  localStorageencryp,
  setItem,
} from "../../../components/helpers/function.jsx";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
import * as moment from "moment-timezone";
import { getData, getDataNoTimeOut, postData } from "../../../config/axios.jsx";
import { reduxForm, reset } from "redux-form";
import { connect } from "react-redux";
import Loading from "react-fullscreen-loading";

class LihatTransaksiMember extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      isLoadingBatal: false,
      point: "POINT",
      datatransaksimember: [],
      
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  
  batalLihatTransaksi() {
    this.setState({
      isLoadingBatal: true,
    });
    setTimeout(() => {
      this.setState({
        isLoadingBatal: false,
        datatransaksimember: [],
      });
      this.props.dispatch(reset("HeadTransaksiMember"));
    }, 1000);
  }
  getKodeMember(e) {
    if (e.length < 10) {
      this.props.change("nama_customer", "");
      this.props.change("no_hp", "");
      this.props.change("alamat_customer", "");
      return false;
    }

    if (this.delayTimer) {
      clearTimeout(this.delayTimer);
    }
    this.delayTimer = setTimeout(() => {
      if (e === undefined) {
        return false;
      }
      // this.setState({
      //   isLoading: true,
      // });
      getData("member/get/by-kode-member/" + e)
        .then((res) => {
          this.setState({
            datatransaksimember: [],
            isLoading: false,
          });
          // console.log(res.data);
        })
        .catch((err) => {
          ToastNotification("info", "Data member tidak ada");
          this.setState({
            datatransaksimember: [],
            isLoading: false,
          });
        });
    }, 1500);
  }
  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    let data_post = {
      kode_member: data.kode_member,
      kode_group: data.kategori,
      kode_dept: data.kode_dept,
      kode_group_barang: data.kode_group,
      kode_lokasi_gudang: data.toko,
      tipe_barang: data.tipe_barang,
      history: data.history === "YA" ? true : false,
      berat_awal: parseFloat(data.berat_awal || 0),
      berat_akhir: parseFloat(data.berat_akhir || 0),
      tgl_awal: formatDate(
        moment(new Date(data.dari_tanggal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.sampai_tanggal)).format("YYYY-MM-DD")
      ),
    };
    if (data.type_pencarian === "BARU") {
      postData("trxmember/get/by-tanggal", data_post)
        .then((res) => {
          if (res.data.length === 0) {
            ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
            this.setState({
              isLoading: false,
              export: false,
              datatransaksimember: [],
            });
          } else {
            ToastNotification("success", "Laporan Transaksi Member Tersedia");
            localStorageencryp("tgl_laporan", JSON.stringify(data_post));
            this.setState({
              export: true,
              isLoading: false,
              datatransaksimember: res.data,
            });
          }
        })
        .catch((error) => {
          ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
          this.setState({
            isLoading: false,
            export: false,
          });
        });
    } else {
      getDataNoTimeOut(
        `trxmember/reports/data-lama?tipe_barang=${data_post.tipe_barang}kode_dept=${data.kode_dept}&kode_group_barang=${data.kode_group_barang}&history=${data_post.history}&tgl_from=${data_post.tgl_awal}&tgl_to=${data_post.tgl_akhir}&kode_member=${data_post.kode_member}&kode_group=${data_post.kode_group}&kode_lokasi_gudang=${data_post.kode_lokasi_gudang}&berat_awal=${data_post.berat_awal}&berat_akhir=${data_post.berat_akhir}`
      )
        .then((res) => {
          if (res.data.length === 0) {
            ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
            this.setState({
              isLoading: false,
              export: false,
              datatransaksimember: [],
            });
          } else {
            ToastNotification("success", "Laporan Transaksi Member Tersedia");
            localStorageencryp("tgl_laporan", JSON.stringify(data_post));
            this.setState({
              datatransaksimember: [],
            });
            this.setState({
              export: true,
              isLoading: false,
              datatransaksimember: res.data,
            });
          }
        })
        .catch((error) => {
          ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
          this.setState({
            isLoading: false,
            export: false,
            datatransaksimember: [],
          });
        });
    }
  }
  componentWillUnmount() {
    setItem("data_member", []);
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Member</Link>
          </li>
          <li className="breadcrumb-item active">Lihat Transaksi Member</li>
        </ol>
        <h1 className="page-header">Lihat Transaksi Member </h1>
        <Panel>
          <PanelHeader>Lihat Transaksi Member</PanelHeader>
          <br />
          {/* Lihat Transaksi Member */}
          <PanelBody>
            <HeadTransaksiMember
              getKodeMember={(e) => this.getKodeMember(e)}
              batalLihatTransaksi={() => this.batalLihatTransaksi()}
              onSubmit={(data) => this.handleSubmit(data)}
              columns={this.state.columns}
              isLoading={this.state.isLoading}
              point={this.state.point}
              isLoadingBatal={this.state.isLoadingBatal}
              datatransaksimember={this.state.datatransaksimember}
            />
            <br />
          </PanelBody>
          <br />
          {/* End Tambah Lihat Transaksi Member  */}
        </Panel>
       
        {this.state.isLoadingBatal || this.state.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </div>
    );
  }
}

LihatTransaksiMember = reduxForm({
  form: "LihatTransaksiMember",
  enableReinitialize: true,
  // validate: ValidasiDataUsers,
})(LihatTransaksiMember);
export default connect(null)(LihatTransaksiMember);
