import {
    React,
    connect,
    reduxForm,
    imgfound
  } from "../../../components/helpers/library";
  
  class ModalGambar extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        isLoading: false,
        settings: "",
      };
    }
  
    render() {
      return (
        <div>
         <img src={this.props.gambar || imgfound} alt="Gambar Barang" width="460px" height="300px" />
        </div>
      );
    }
  }
  
  ModalGambar = reduxForm({
    form: "ModalGambar",
    enableReinitialize: true,
  })(ModalGambar);
  export default connect(null)(ModalGambar);
  